.block-FB05 {
	// Set theme colours
	@include theme('background-color', '.inner-block', '&');
	@include themeAlt('background-color', '.inner-block:after', '&');

	background: $color-lightgrey-3;
	padding: 0;

	// Set default inner block colour
	.inner-block {
		background-color: $color-red;

		&:after {
			background-color: $color-alt-red;
		}

		svg path {
			fill: $color-alt-red;
		}
	}

	.corner-arrow {
		position: absolute;
		top: 0;
		right: 0;
		width: 67px;
		height: auto;
	}

	.FB05__main {
		padding-right: 67px;
		padding: 50px 0 50px 0;
	}

	.FB05__side {
		.FB05__side-image {
			p {
				width: 100%;
				height: 100%;
				margin: 0;
			}
		}

		.inner-block {
			padding-right: 50px;
		}

		img {
			object-fit: cover;
    		width: 100%;
   			height: 100%;
		}

		h1,
		h2,
		h3,
		h4,
		h5,
		h6 {
			font-size: 21px;
		}
	}
	
	.row {
		margin: 0;
		width: 100%;

		> div {
			padding: 0;
		}
	}

	@include lg {
		&.height--large {
			.FB05__main {
				padding: 65px 100px 65px 0;
			}
		}

		.corner-arrow {
			width: 124px;
			height: 100%;
		}

		.FB05__main {
			position: relative;
			padding: 20px 124px 20px 0;
		}

		.half-container {
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
		}

		.FB05__side {
			display: flex;
			height: 100%;

			.inner-block {
				width: 311px;
				padding-right: 7vw;
			}

			.FB05__side-image {
				flex: 1;
			}
		}
	}
}