.block-PTB01 {
	.PTB01-carousel {
		.slick-dots {
			list-style: none;
			margin: 0;
			padding: 0 0 0 32px;
			padding: 0;

			li {
				display: inline-block;
				margin: 0 14px 0 0;
				font-size: 14px;

				a {
					text-decoration: none;
					border-bottom: 1px solid transparent;
					cursor: pointer;
					font-weight: 600;
					color: $color-text !important;
				}

				&.slick-active {
					a {
						border-color: $color-text;
					}
				}
			}
		}

		.slick-arrow {
			padding: 0;
			background: none;
			border: 0;
			width: auto;
			height: auto;
			position: absolute;
			top: 50%;
			left: -15px;
			margin-top: -17px;

			&.slick-next {
				left: auto;
				right: -15px;
			}
		}

		.PTB01-carousel__item {
			.PTB01-carousel__item-image {
				margin: 0 0 30px;
				height: 270px;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}
	}

	@include md {
		.PTB01-carousel {
			padding: 0 0 30px;

			.PTB01-carousel__item {
				.PTB01-carousel__item-image {
					margin: 0;
				}

				.row > div:nth-child(2) {
					display: flex;
					align-items: center;
				}
			}

			.slick-arrow {
				left: -47px;

				&.slick-next {
					right: -47px;
				}
			}

			.slick-dots {
				position: absolute;
				left: calc(50% + $bs_grid_default_spacing);
				bottom: 0;
			}
		}

		&.media-align--right {
			.PTB01-carousel {
				.PTB01-carousel__item {
					.row {
						> div {
							&:first-child {
								order: 2;
							}

							&:last-child {
								order: 1;
							}
						}
					}
				}

				.slick-dots {
					left: 0;
				}
			}
		}
	}
}